@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-float infinite 3s ease-in-out;
            animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@-webkit-keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px)
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px)
  }
}

@keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px)
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px)
  }
}

.row {
    color: white;
    margin-left: 20px;
  }
  
  .row__posters {
    display: -webkit-flex;
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
  }
  
  .row__posters::-webkit-scrollbar {
    display: none;
  }
  
  .row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
  }
  
  .row__poster:hover {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
  
  .row__posterLarge {
    max-height: 250px;
  }
  
  .row__posterLarge:hover {
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
  }
.banner {
    color: white;
    object-fit: contain;
    height: 448px;
  }
  
  .banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
  }
  
  .banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
  }
  
  .banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
  }
  
  .banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
  }
  
  .banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
  }
  
  .banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
    );
  }
  
.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 30px;
  z-index: 1;
  /* Animations */
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav__black {
  background-color: #111;
}

.nav__logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}

.nav__avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}
body {
	 background: #221f1f;
	 color: #fff;
	 font-family: 'Lato', sans-serif;
}
 h2, .TitleList .Title {
	 font-size: 24px;
	 font-weight: 600;
	 line-height: 1.4;
	 margin-bottom: 1em;
}
 p {
	 font-size: 15px;
	 font-weight: 300;
	 line-height: 1.6;
	 margin-bottom: 1em;
}

.nav__black {
    background-color: #111;
  }

 .Header {
	/* FF3.6-15 */
	/* Chrome10-25,Safari5.1-6 */
	 background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	 filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6000000', endColorstr='#00000000',GradientType=0);
	/* IE6-9 */
	 display: -webkit-flex;
	 display: flex;
	 padding: 40px;
	 position: fixed;
	 top: 0;
	 left: 0;
	 width: 100vw;
	 box-sizing: border-box;
	 z-index: 5;
}
 .Logo {
	 width: 151px;
	 height: 41px;
	 margin-right: 10px;
	 transition: margin 0.125s ease;
}
 .Logo svg {
	 -webkit-transform: scale(0.5);
	         transform: scale(0.5);
	 -webkit-transform-origin: 0 0;
	         transform-origin: 0 0;
}
 .Logo svg path {
	 fill: #e50914;
	 transition: fill 0.125s ease;
}
 .Logo:hover {
	 cursor: pointer;
	 margin-top: 2px;
}
 .Logo:hover svg path {
	 fill: #fff !important;
}
 .Navigation ul {
	 display: -webkit-flex;
	 display: flex;
	 height: 37px;
	 -webkit-align-items: center;
	         align-items: center;
	 padding: 0 10px;
}
 .Navigation ul li {
	 font-weight: 400;
	 padding: 7px 10px;
	 font-size: 14px;
	 transition: background 0.125s ease;
	 border-radius: 3px;
}
 .Navigation ul li:hover {
	 background: #e50914;
	 cursor: pointer;
}
 .Search {
	 -webkit-align-items: center;
	         align-items: center;
	 -webkit-justify-content: center;
	         justify-content: center;
	 width: 100%;
	 position: absolute;
	 display: -webkit-flex;
	 display: flex;
	 justify-content: center;
	 pointer-events: none;
}
 .Search input {
	 font-family: 'Lato', sans-serif;
	 font-weight: 300;
	 font-size: 14px;
	 height: 37px;
	 width: 20%;
	 background: transparent;
	 color: #fff;
	 -webkit-appearance: none;
	         appearance: none;
	 border: 0;
	 border: 2px solid rgba(245, 245, 241, .1);
	 outline: none;
	 border-radius: 37px;
	 padding: 0 10px;
	 pointer-events: all;
	 transition: border 0.25s ease, width 0.125s ease 0.125s;
}
 .Search input:focus {
	 border: 2px solid #e50914;
	 width: 30%;
}
 .UserProfile {
	 margin-left: auto;
}
 .UserProfile .User {
	 display: -webkit-flex;
	 display: flex;
}
 .UserProfile .User .image {
	 border-radius: 44px;
	 margin-left: 10px;
	 overflow: hidden;
	 width: 44px;
	 height: 44px;
	 box-sizing: border-box;
	 border: 3px solid transparent;
	 transition: border 0.125s ease;
}
 .UserProfile .User .image img {
	 width: 100%;
	 display: block;
}
 .UserProfile .User .name {
	 -webkit-align-items: center;
	         align-items: center;
	 display: -webkit-flex;
	 display: flex;
	 font-size: 18px;
	 font-weight: 300;
	 height: 44px;
}
 .UserProfile .User:hover {
	 cursor: pointer;
}
 .UserProfile .User:hover .image {
	 border: 3px solid #e50914;
}
 .UserProfile .UserProfile-menu {
	 display: none;
}
 .Hero {
	 width: 100%;
	 position: relative;
	 background-size: cover;
	 min-height: 800px;
}
 .Hero .content {
	 position: relative;
	 z-index: 4;
	 width: 500px;
	 left: 10vw;
	 top: 10vw;
}
 .Hero .content .logo {
	 max-width: 400px;
	 display: block;
	 position: relative;
	 left: -5px;
}
 .Hero .content p {
	 width: 100%;
}
 .Hero .content .button-wrapper {
	 display: -webkit-flex;
	 display: flex;
	 width: 400px;
	 margin-top: 30px;
}
 .Hero .content .button-wrapper .Button:first-child {
	 margin-right: 10px;
}
 .Hero .overlay {
	/* FF3.6-15 */
	/* Chrome10-25,Safari5.1-6 */
	 background: linear-gradient(to bottom, rgba(34, 31, 31, 1) 0%, rgba(34, 31, 31, 0) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	 filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6000000', endColorstr='#00000000',GradientType=0);
	/* IE6-9 */
	 height: 100%;
	 position: absolute;
	 z-index: 3;
	 top: 0;
	 left: 0;
	 width: 100%;
	 -webkit-transform: rotate(180deg);
	         transform: rotate(180deg);
}
 .Button {
	 background: transparent;
	 display: -webkit-flex;
	 display: flex;
	 height: 44px;
	 -webkit-align-items: center;
	         align-items: center;
	 -webkit-justify-content: center;
	         justify-content: center;
	 text-decoration: none;
	 color: #fff;
	 padding: 20px;
	 box-sizing: border-box;
	 border: 2px solid rgba(245, 245, 241, .2);
	 border-radius: 44px;
	 font-size: 14px;
	 font-weight: 600;
	 transition: border 0.125s ease, background 0.125s ease;
}
 .Button:hover {
	 border: 2px solid #f5f5f1;
}
 .Button[data-primary='true'] {
	 border: 2px solid #e50914;
}
 .Button[data-primary='true']:hover {
	 background: #e50914;
}
 .Hero ~ .TitleList:nth-child(3) {
	 margin-top: -10vw;
	 position: relative;
	 z-index: 4;
}
 .TitleList {
	 padding: 20px 40px;
	 box-sizing: border-box;
	 transition: opacity 3s ease;
	 opacity: 0;
}
 .TitleList .titles-wrapper {
	 display: -webkit-flex;
	 display: flex;
	 -webkit-flex-wrap: wrap;
	         flex-wrap: wrap;
	 margin: 20px 0;
	 width: calc(100vw - 80px);
	 box-sizing: border-box;
	 -webkit-justify-content: space-between;
	         justify-content: space-between;
}
 .TitleList[data-loaded='true'] {
	 opacity: 1;
}
 .Item {
	 width: calc(20% - 10px);
	 min-width: calc(20% - 10px);
	 background-color: #000;
	 -webkit-flex: 1 0 auto;
	         flex: 1 0 auto;
	 background-position: center;
	 background-size: 100%;
	 height: 200px;
	 background-repeat: no-repeat;
	 overflow: hidden;
	 margin-right: 10px;
	 transition: background 1s ease;
}
 .Item:nth-child(n+6) {
	 margin-top: 20px;
}
 .Item:last-child {
	 margin-right: 0;
}
 .Item .overlay {
	/* FF3.6-15 */
	/* Chrome10-25,Safari5.1-6 */
	 background: linear-gradient(to bottom, rgba(0, 0, 0, .75) 0%, rgba(0, 0, 0, 0) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	 filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#a6000000', endColorstr='#00000000',GradientType=0);
	/* IE6-9 */
	 padding: 20px;
	 position: relative;
	 height: 100%;
	 pointer-events: none;
	 opacity: 0;
	 transition: opacity 0.125s ease;
}
 .Item .overlay .title {
	 font-size: 22px;
	 font-weight: 200;
	 opacity: 0;
	 transition: opacity 0.25s ease;
}
 .Item .overlay .rating {
	 font-size: 14px;
	 opacity: 0;
	 transition: opacity 0.25s ease 0.125s;
}
 .Item .overlay .plot {
	 font-size: 14px;
	 display: -webkit-box;
	 margin-top: 100px;
	 font-weight: 300;
	 line-height: 1.6;
	 opacity: 0;
	 -webkit-line-clamp: 3;
	 -webkit-box-orient: vertical;
	 overflow: hidden;
	 text-overflow: ellipsis;
	 transition: margin 0.25s ease 0.125s, opacity 0.25s ease 0.25s;
}
 .Item:hover {
	 background-size: 150%;
}
 .Item:hover .overlay {
	 opacity: 1;
	 pointer-events: all;
}
 .Item:hover .overlay .plot {
	 margin-top: 10px;
	 opacity: 1;
}
 .Item:hover .overlay .title {
	 opacity: 1;
}
 .Item:hover .overlay .rating {
	 opacity: 1;
}
 .Item:hover .overlay .ListToggle {
	 opacity: 1;
}
 .ListToggle {
	 border: 2px solid rgba(255, 255, 255, .2);
	 width: 32px;
	 height: 32px;
	 font-size: 16px;
	 border-radius: 32px;
	 overflow: hidden;
	 position: absolute;
	 right: 20px;
	 top: 20px;
	 opacity: 0;
}
 .ListToggle:hover {
	 border: 2px solid #fff;
}
 .ListToggle:hover div {
	 top: - 32px;
}
 .ListToggle[data-toggled="true"] {
	 background: #e50914;
	 border: 2px solid #e50914;
}
 .ListToggle[data-toggled="true"] div {
	 top: - 32px;
}
 .ListToggle div {
	 position: absolute;
	 top: 0;
	 left: 0;
	 height: 64px;
	 width: 32px;
	 transition: 0.125s ease;
}
 .ListToggle div i {
	 display: -webkit-flex;
	 display: flex;
	 -webkit-align-items: center;
	         align-items: center;
	 -webkit-justify-content: center;
	         justify-content: center;
	 height: 32px;
	 width: 32px;
}
 
